import React from 'react';
import { login } from '../../utils';

import './signin.sass';

const SignIn = (props) => {

  const handleLogin = () => {
    login();
    props.history.push('/admin-page');
  };


  return (
    <div className="signin-page">
      <form className="fast-buy">
        <h2>Авторизация</h2>
        <input type="password" placeholder="Введите пароль" />
        <button className="btn-clear auth-btn" onClick={() => handleLogin()}>Войти в админку</button>
      </form>
    </div>
  );
};

export default SignIn;
